/*
=============== 
Variables
===============
*/

:root{
  /* primary / main color  */
  --clr-primary-1:#0071bc;
  /* dark shades of main color */
  --clr-primary-2:#005a96;
  --clr-primary-3:#004471;
  --clr-primary-4:#002d4b;
  /* light shades of main color */
  --clr-primary-5:#338dc9;
  --clr-primary-6:#65aad7;
  --clr-primary-7:#99c6e4;
  /* secondary color  */
  --clr-secondary-1:#c1272d;
  /* dark shades of secondary color */
  --clr-secondary-2:#9a1f24;
  --clr-secondary-3:#74171b;
  --clr-secondary-4:#4d1012;
  /* light shades of secondary color */
  --clr-secondary-5:#cd5257;
  --clr-secondary-6:#da7d81;
  --clr-secondary-7:#e6a8aa;
  /* darkest grey - used for headings */
  --clr-grey-1: #102a42;
  --clr-grey-2: #243a52;
  --clr-grey-3: #324d67;
  --clr-grey-4: #48647f;
  /* grey used for paragraphs */
  --clr-grey-5: #617d98;
  --clr-grey-6: #829ab0;
  --clr-grey-7: #9eb2c7;
  --clr-grey-8: #bcccdc;
  --clr-grey-9: #dae2ec;
  --clr-grey-10: #f1f5f8;
  --clr-white: #fff;
  --clr-black: #000;
  /* font variables */
  --ff-primary: 'Roboto', sans-serif;
  --ff-secondary: 'Open Sans', sans-serif;
  /* misc variables */
   --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}

/*
=============== 
Global Styles
===============
*/

*,
::after,
::before{
  margin : 0;
  padding : 0;
  box-sizing : border-box;
}
body{
  font-family: 'Open Sans', sans-serif;
  font-family: var(--ff-secondary);
  background: #f1f5f8;
  background: var(--clr-grey-10);
  color: #102a42;
  color: var(--clr-grey-1);
  line-height: 1.5;
  font-size: 0.875rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}

.logo-container{
  width : 50vw;
}
@media screen and (max-width:900px){
  .logo-container{
    width : 30vw;
  }
}
@media screen and (max-width:600px){
  .logo-container{
    width : 90vw;
  }
}
img{
  width : 70%;
  display: block;
  margin : 0 auto;
}
@media screen and (max-width:900px){
  img{ width:100%;}
}
@media screen and (max-width:600px){
  img{ width:70%; margin : 0 auto;}
}
.search {
  width: 50vw;
  max-width: 1170px;
  max-width: var(--max-width);
  margin: 0 auto;
}
@media screen and (max-width:900px){
  .search{
    margin : 0;
  }
}
@media screen and (max-width:600px){
  .search{
    width:90vw;
    margin:1rem auto 0 auto;
  }
}
.search-form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.form-input {
  width: 60%;
}
.form-input,
.submit-btn{
  padding: 0.75rem 0;
  text-transform: capitalize;
  letter-spacing: 0.1rem;
  letter-spacing: var(--spacing);   
  color: #004471;   
  color: var(--clr-primary-3);
  background: transparent;
  font-size: 1.5rem;
  border: none;
  border-bottom: 2px solid #65aad7;
  border-bottom: 2px solid var(--clr-primary-6);
}
.submit-btn{cursor: pointer;}

.form-input {
  color: #324d67;
  color: var(--clr-grey-3);
  padding-left: 1rem;
}
.form-input:focus,
.form-input:active{
  outline:none;
  
}
.form-input::-webkit-input-placeholder {
  text-transform: none;
  color: #bcccdc;
  color: var(--clr-grey-8);
  font-size: 1.2rem;
}
.form-input:-ms-input-placeholder {
  text-transform: none;
  color: #bcccdc;
  color: var(--clr-grey-8);
  font-size: 1.2rem;
}
.form-input::placeholder {
  text-transform: none;
  color: #bcccdc;
  color: var(--clr-grey-8);
  font-size: 1.2rem;
}
@media screen and (max-width:900px){
  .form-input::-webkit-input-placeholder {
  font-size: 1rem;
}
  .form-input:-ms-input-placeholder {
  font-size: 1rem;
}
  .form-input::placeholder {
  font-size: 1rem;
}
}
@media screen and (max-width:500px){
  .form-input::-webkit-input-placeholder {
  font-size: .8rem;
}
  .form-input:-ms-input-placeholder {
  font-size: .8rem;
}
  .form-input::placeholder {
  font-size: .8rem;
}
}

h1,
h2,
h3,
h4 {
  letter-spacing: 0.1rem;
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
  font-family: 'Roboto', sans-serif;
  font-family: var(--ff-primary);
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.5rem;
}
h4 {
  font-size: 1rem;
}
p {
  margin-bottom: 1.25rem;
  color: #617d98;
  color: var(--clr-grey-5);
}
@media screen and (max-width: 900px) {
  h1 {
    font-size: 2.5rem;
  }
  h2 {
    font-size: 1.5rem;
  }
  h3 {
    font-size: 1rem;
  }
  h4 {
    font-size: .75rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}

.underline {
  height: 0.25rem;
  width: 5rem;
  background: #0071bc;
  background: var(--clr-primary-1);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 3rem;
}
/* section */
.section {
  padding: 1rem 0;
  position: relative;
}

.section-center {
  width: 90vw;
  margin: 0 auto;
  max-width: 1170px;
}
@media screen and (min-width: 992px) {
  .section-center {
    width: 95vw;
  }
}
.loading-img {
  width: 10rem;
  height: 10rem;
  display: block;
  margin: 0 auto;
  margin-top: 10rem;
} 

main {
  padding-bottom: 2rem;
}



